@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.corp-info {
  &-page {
    padding-bottom: 128.59px;

    @include mixin.wide-tablet {
      padding-bottom: 80px;
    }

    @include mixin.small-tablet {
      padding-bottom: 64px;
    }

    .page__title {
      margin-bottom: 24px;
    }

    .page__subtitle {
      p {
        margin: 0;
        text-indent: 55px;
        color: var.$gray;

        @include mixin.wide-tablet {
          text-indent: 25px;
        }
      }
    }

    &__tab-links {
      display: flex;
      align-items: center;
      gap: 32px 64px;
      padding-bottom: 27px;
      border-bottom: 1px solid #C4C8CF;
      margin-bottom: 36px;

      @include mixin.wide-tablet {
        gap: 16px 32px;
        font-size: 1.6rem;
        line-height: 1.2;
      }
    }

    &__tab-link {
      font-size: 2.4rem;
      font-weight: 700;
      line-height: 1.66;
      color: #B1B5BD;
      transition: 0.3s linear;
      cursor: pointer;

      @include mixin.wide-tablet {
        font-size: 2rem;
        line-height: 1.33;
      }

      &--active {
        color: var.$secondary;
      }
    }

    &__tab-body {
      display: none;

      &--active {
        display: block;
      }
    }
  }

  &__title-4 {
    font-weight: 700;
  }

  &__head {
    margin-bottom: 48px;

    @include mixin.wide-tablet {
      margin-bottom: 32px;
    }

    .row {
      gap: 16px 0;
    }
  }

  &__head-title {
    margin-bottom: 32px;

    @include mixin.wide-tablet {
      margin-bottom: 16px;
    }
  }

  &__tabs {
    margin-bottom: 64px;

    @include mixin.wide-tablet {
      margin-bottom: 32px;
    }
  }

  &__tab {
    padding: 12px 24px;

    &:hover {
      background: var.$primary;
      color: var.$light;
    }

    &.badge--orange {
      color: var.$light;
    }
  }
}

.corp-info-item {
  padding-bottom: 48px;
  margin-bottom: 48px;
  border-bottom: 1px solid #E4E4E4;

  @include mixin.wide-tablet {
    padding-bottom: 24px;
    margin-bottom: 24px;
  }

  &--active {
    @include mixin.wide-tablet {
      padding-bottom: 7px;
      border-color: transparent;
    }
  }

  &:last-child {
    padding-bottom: 0;
    margin-bottom: 100px;
    border: none;

    @include mixin.wide-tablet {
      margin-bottom: 80px;
    }

    @include mixin.small-tablet {
      margin-bottom: 64.59px;
    }
  }

  &__head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.66;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    @include mixin.wide-tablet {
      font-size: 2.2rem;
    }

    @include mixin.small-tablet {
      font-size: 2rem;
      line-height: 1.6;
    }

    a {
      margin-top: 16px;
      color: var.$gray;
      font-family: var.$font-family;
      font-size: 2.2rem;
      font-weight: 300;
      line-height: 1.45;
      letter-spacing: 0.3px;
      text-decoration-line: underline;

      &:hover {
        color: var.$gray;
      }
    }
  }

  &:hover &__title {
    color: var.$primary;
    text-decoration-line: underline;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 100px;
    background: rgba(var.$secondary, 0.10);
    transition: 0.3s linear;
    transform-origin: center;

    svg {
      width: 24px;
      height: 24px;

      path {
        stroke: var.$secondary;
        transition: 0.3s linear;
      }
    }
  }

  &:hover &__btn {
    background: rgba(var.$primary, 0.10);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--active &__btn {
    background: rgba(var.$primary, 0.10);
    transform: rotateZ(180deg);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--links &__btn {
    background: rgba(var.$primary, 0.10);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--active.procurement-item--active &__btn {
    transform: rotateZ(0deg);
  }

  &__body {
    margin-top: 0;
    max-height: 0;
    display: flex;
    gap: 16px 0;
    flex-wrap: wrap;
    transition: 0.3s linear;
    overflow: hidden;
  }

  &--active &__body {
    max-height: 600px;
    margin-top: 32px;

    @include mixin.wide-tablet {
      margin-top: 24px;
    }

    @include mixin.small-tablet {
      margin-top: 16px;
    }
  }

  &.transitionend &__body {
    overflow-y: auto;
  }
}

.director {
  margin-bottom: 33px;
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.66;
  color: var.$secondary;

  &:after {
    content: '';
    display: block;
    width: 100%;
    clear: both;
  }

  p {
    &:last-child {
      margin-bottom: 0;
    }

    @include mixin.wide-tablet {
      margin-bottom: 20px;
    }

    strong {
      font-weight: bold;
    }
  }

  table {
    width: 100%;
    border-collapse: collapse;

    th {
      font-weight: bold;
    }

    td, th {
      padding: 5px 11px;
      border: 1px solid #000;
    }
  }

  ul {
    list-style: disc;
    padding-left: 20px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__role {
    font-size: 2.2rem;
    font-weight: 500;
    line-height: 1.66;
    color: var.$secondary;
    margin-bottom: 28px;
    max-width: 920px;

    @include mixin.wide-tablet {
      font-size: 1.8rem;
      line-height: 1.33;
    }
  }

  &__img {
    float: left;
    width: 217px;
    height: 217px;
    overflow: hidden;
    margin-right: 62px;
    padding: 0;

    @include mixin.wide-tablet {
      margin-bottom: 18px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.66;
    color: var.$secondary;
    margin-bottom: 27px;

    @include mixin.wide-tablet {
      font-size: 1.8rem;
      line-height: 1.33;
    }
  }
}